import React, { useEffect } from 'react';
import { Box, Typography, Grid, Paper } from '@mui/material';
import { BarChart } from '@mui/x-charts/BarChart';
import { useState } from 'react';

export const PVdesign = ({ pvOption }) => {
    const [totalPrice, setTotalPrice] = useState(0);
    const [totalPower, setTotalPower] = useState(0);
    const [totalPanelsWeights, setTotalPanelsWeights] = useState(0);
    const [inverter, setInverter] = useState(null);
    const [panel, setPanel] = useState(null);
    const [charge, setCharge] = useState(null);
    const [dataset, setDataset] = useState([]);
    const [insolationForecast, setInsolationForecast] = useState(null);
    // const [additionalElements, setAdditionalElements] = useState(null);

    useEffect(() => {
        if (pvOption) {
            setTotalPrice(pvOption.total_price);
            setTotalPower(pvOption.total_power_kW);
            setTotalPanelsWeights(pvOption.total_panel_weight_kg);
            setInverter(pvOption.inverter);
            setPanel(pvOption.panel);
            if (pvOption.charge) {
                setCharge(pvOption.charge);
            }

            const monthes = ['', '', '', 'V', 'V', 'V', 'V', 'V', 'X', 'X', 'X', 'X'];

            const prepearedDataBarChart = pvOption?.insolation_forecast?.map((item, idx) => {
                return {
                    month: monthes[idx],
                    data: item
                }
            });

            setDataset(prepearedDataBarChart);
            setInsolationForecast(pvOption.year_production);

            console.log(pvOption);
        }
    }, [pvOption]);

    return (
        <Box display="flex" flexDirection="column" gap={1}>
            <Typography variant="h5" gutterBottom>
                 
            </Typography>
            <Grid container spacing={2}>
                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{ p: 2 }}>
                        <Typography variant="subtitle1" sx={{ textAlign: 'left', fontWeight: 'bold' }}> :</Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>{totalPower || ' '} </Typography>
                        <Typography sx={{ textAlign: 'left' }} variant="subtitle1"> :</Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>{totalPrice || ' '} </Typography>
                        <Typography sx={{ textAlign: 'left' }} variant="subtitle1">  :</Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>{totalPanelsWeights || ' '} </Typography>
                        <Typography sx={{ textAlign: 'left' }} variant="subtitle1">  -  :</Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>{insolationForecast || ' '} *</Typography>
                    </Paper>
                </Grid>

                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{ p: 2 }}>
                        <Typography variant="h6" gutterBottom sx={{ textAlign: 'left', fontWeight: 'bold' }}></Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {inverter?.producer || ' '} </Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {inverter?.model || ' '}</Typography>
                        <Typography sx={{ textAlign: 'left' }}>: {inverter?.type || ' '}</Typography>
                        <Typography sx={{ textAlign: 'left' }}>: {inverter?.price || ' '} </Typography>
                    </Paper>
                </Grid>

                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{ p: 2 }}>
                        <Typography variant="h6" gutterBottom sx={{ textAlign: 'left', fontWeight: 'bold' }}> </Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {panel?.producer || ' '}</Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {panel?.model || ' '}</Typography>
                        <Typography sx={{ textAlign: 'left' }}>: {panel?.type || ' '}</Typography>
                        <Typography sx={{ textAlign: 'left' }}>: {panel?.price || ' '} </Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {panel?.orientation || ' '}</Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}> : {panel?.panel_type_connection || ' '}</Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}> : {panel?.number_panels_in_system || ' '}</Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>. : {panel?.max_pv_power_for_params_kW || ' '} </Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {panel?.voltage || ' '} </Typography>
                        <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {panel?.current || ' '} </Typography>
                    </Paper>
                </Grid>

                {charge && (
                    <Grid item xs={12} md={4}>
                        <Paper elevation={3} sx={{ p: 2 }}>
                            <Typography variant="h6" gutterBottom sx={{ textAlign: 'left', fontWeight: 'bold' }}></Typography>
                            <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {charge?.producer || ' '} </Typography>
                            <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {charge?.seria || ' '}</Typography>
                            <Typography sx={{ textAlign: 'left', fontWeight: 'bold' }}>: {charge?.model || ' '}</Typography>
                            <Typography sx={{ textAlign: 'left' }}>: {charge?.battery_technology || ' '}</Typography>
                            <Typography sx={{ textAlign: 'left' }}>: {charge?.price || ' '} </Typography>
                            <Typography sx={{ textAlign: 'left' }}> : {charge?.charge_count || ' '}</Typography>
                            <Typography sx={{ textAlign: 'left' }}> : {charge?.total_charge_capacity_kWh || ' '} *</Typography>
                        </Paper>
                    </Grid>
                )}

                <Grid item xs={12} md={4}>
                    <Paper elevation={3} sx={{ p: 2 }}>
                        <Typography variant="h6" gutterBottom sx={{ textAlign: 'left', fontWeight: 'bold' }}>    </Typography>

                        <BarChart
                            height={290}
                            dataset={dataset}
                            series={[{ dataKey: 'data', label: '' }]}
                            xAxis={[{ dataKey: 'month', label: '' }]}
                        />
                    </Paper>
                </Grid>


            </Grid>

        </Box>
    );
};
